// import ko from '../../lib/knockout-3.4.2';

export default {
    viewModel: class HeaderViewModel {
        constructor(params) {
            this.copyright = ko.pureComputed(() => params.customization().copyright);
        }
    },
    template: `
        <div class="aldi-footer">
            <span class="aldi-copyright" data-bind="text: copyright"></span>
        </div>
    `
};
