import AuditListModel from './models/auditListModel';
import CustomizationModel from './models/customizationModel';
import ConfigurationModel from './models/configurationModel';

export default class AppViewModel {
    constructor(api) {
        this.api = api;

        this.customization = ko.observable();
        this.configuration = ko.observable();
        this.auditList = ko.observable();
        this.selectedRow = ko.observable();

        this.isLoadingData = ko.observable(false);
        this.isInitializing = ko.observable(true);
    }

    initialize() {
        this.whenLoaded = this.api.init()
            .then(() => this._loadConfig())
            .then(() => this._loadEvents(this.configuration().initialUrl))
            .then(() => {
                this.isInitializing(false);
            })
            .catch((err) => {
                this._reportError('Error loading data from server', err);
            });
    }

    forceRefresh() {
        return this.api.init()
            .then(() => this._loadConfig())
            .then(() => this._loadEvents(this.configuration().initialUrl))
            .catch((err) => {
                this._reportError('Error loading data from server', err);
            });
    }

    selectRow(row) {
        this.selectedRow(row);
    }

    clearSelectedRow() {
        this.selectedRow(null);
    }

    _reportError(humanReadableMessage, err) {
        this.api.reportError(err);
        console.error(err);
        // TODO display message to end user
    }

    _loadConfig() {
        this.isLoadingData(true);
        return this.api.getConfig()
            .then((data) => {
                this.customization(new CustomizationModel(data));
                this.configuration(new ConfigurationModel(data));
                this.isLoadingData(false);
            });
    }

    _loadEvents(url) {
        this.isLoadingData(true);
        return this.api.getEntries(url)
            .then((data) => {
                if (data === null) {
                    // sample/iframe system
                    this.auditList(null);
                    return;
                }

                // init a new model if first load, otherwise apply update
                //  to model in place (instead of recreating the world)
                if (this.auditList() == null) {
                    this.auditList(new AuditListModel(data));
                }
                else {
                    this.auditList().update(data);
                }
                this.isLoadingData(false);
            });
    }

    onNavigate(navigationLink) {
        this._loadEvents(navigationLink.url);
    }
}
