import API from './api';

export default class APIFake extends API {
    constructor() {
        super({
            baseUrl: 'http://0.0.0.0/'
        });

        this.data = {
            config: {},
            entries: null
        };
    }

    getConfig() {
        return $.Deferred().resolve(this.data.config);
    }

    getEntries() {
        return $.Deferred().resolve(this.data.entries);
    }

    setData(freshData) {
        this.data = freshData;
    }
}
