
// import $ from '../lib/jquery-3.3.1.min';
import samples from './samples';

export default class Api {
    constructor(options) {
        this.options = options;
    }

    init() {
        if (this._init === undefined) {
            this._init = $.Deferred().resolve();
        }
        return this._init;
    }

    getConfig() {
        const { baseUrl } = this.options;
        return $.Deferred().resolve({
            id: 'some-guid',
            initial: {
                url: 'initial entries url'
            },
            custom: {
                url: 'https://www.audotlog.co/',
                logo: 'images/logo56-alt3.png',
                title: 'auditlog',
                headerLinks: [
                    { url: 'https://app.auditlog.co', label: 'Home' },
                    { url: 'mailto://contact@auditlog.co', label: 'Contact' }
                ],
                copyright: 'Copyright AuditLog.co, 2018, All rights reserved.'
            },
            columns: [
                /* eslint-disable */
                { order: 0, label: 'Event', lines: [{ field: 'action' }, { field: 'description' }] },
                { order: 1, label: 'Actor', lines: [{ field: 'actor.email' }] },
                { order: 2, label: 'Source', lines: [{ field: 'context.client.ip_address' }] },
                { order: 3, label: 'Item Changed', lines: [{ field: 'target.type' }, { field: 'target.label' }] },
                { order: 4, label: 'Time', lines: [{ field: 'time[time]' }] }
                /* eslint-enable */
            ]
        });
    }

    getEntries(url) {
        const { baseUrl } = this.options;
        return $.Deferred().resolve({
            _id: {
                label: 'March 2018',
                type: 'GET',
                href: baseUrl + url,
                timetamp: 'abc'
            },
            _links: {
                next: { label: 'April 2018', type: 'GET', href: 'api/auditLog/view-xyz/201804' },
                previous: { label: 'February 2018', type: 'GET', href: 'api/auditLog/view-xyz/201802' }
            },
            entries: samples.entries
        });
    }

    reportError(error) {
        this.lastError = error;
    }
}
