// import knockout from '../lib/knockout-3.4.2';

import Api from './api';
import ApiFake from './apiFake';
import AppViewModel from './appViewModel';

import underscoreTemplateEngine from './utils/underscoreTemplateEngine';
import applyBindingDetailsClick from './utils/applyBindingDetailsClick';
import applyBindingPotentialLink from './utils/applyBindingPotentialLink';
import header from './components/header';
import entryTable from './components/entryTable';
import navigation from './components/navigation';
import footer from './components/footer';

export default class App {
    constructor(options) {
        App._validateOptions(options);
        App._initializeKnockout(ko);

        this.api = new Api(options);
        this.viewmodel = new AppViewModel(this.api);
    }

    static _initializeKnockout(ko) {
        underscoreTemplateEngine(ko);
        applyBindingDetailsClick(ko, $);
        applyBindingPotentialLink(ko, $);
        ko.components.register('ko-header', header);
        ko.components.register('ko-navigation', navigation);
        ko.components.register('ko-audit-list', entryTable);
        ko.components.register('ko-footer', footer);
    }

    static _validateOptions(options) {
        const errors = [];

        if (options == null) {
            throw new Error('Configuration error: missing required options parameter');
        }

        if (options.readToken === undefined) {
            errors.push('readToken');
        }

        if (errors.length > 0) {
            throw new Error(`Configuration error: missing required options ${errors.join(', ')}`);
        }
    }

    mount() {
        this.viewmodel.initialize();
        ko.applyBindings(this.viewmodel);
    }

    // Used for configuration in the application, allows the external frame
    //  to update configurations and data
    applySampleOverride() {
        this.api = new ApiFake();
        this.viewmodel.api = this.api;
        let isMounted = false;

        return (event) => {
            this.api.data = JSON.parse(event.data);
            if (!isMounted) {
                this.mount();
                isMounted = true;
            }
            else {
                this.viewmodel.forceRefresh();
            }
        };
    }
}

window.App = App;
