// import ko from '../../lib/knockout-3.4.2';

const ICON = {
    PREVIOUS: '<i class="icon-left-open"></i>',
    NEXT: '<i class="icon-right-open"></i>'
};

export default {
    viewModel: class NavigationViewModel {
        constructor(params) {
            this.previousLink = ko.pureComputed(() => params.auditList().previousLink());
            this.currentLabel = ko.pureComputed(() => params.auditList().currentLabel());
            this.nextLink = ko.pureComputed(() => params.auditList().nextLink());
        }
    },
    template: `
        <!-- ko if: previousLink -->
        <a href="#" class="aldi-navlink" data-bind="attr: { href: previousLink().url, title: previousLink().label }">${ICON.PREVIOUS}</a>
        <!-- /ko -->
        <!-- ko ifnot: previousLink -->
        <a href="#" class="aldi-navlink aldi-navlink-disabled">${ICON.PREVIOUS}</a>
        <!-- /ko -->

        <span class="aldi-curlink" data-bind="text: currentLabel"></span>

        <!-- ko if: nextLink -->
        <a href="#" class="aldi-navlink" data-bind="attr: { href: nextLink().url, title: nextLink().label }">${ICON.NEXT}</a>
        <!-- /ko -->
        <!-- ko ifnot: nextLink -->
        <a href="#" class="aldi-navlink aldi-navlink-disabled">${ICON.NEXT}</a>
        <!-- /ko -->
    `
};
