// import ko from '../../lib/knockout-3.4.2';

export default {
    viewModel: class HeaderViewModel {
        constructor(params) {
            this.url = ko.pureComputed(() => params.customization().url);
            this.logo = ko.pureComputed(() => params.customization().logo);
            this.title = ko.pureComputed(() => params.customization().title);
            this.headerLinks = ko.pureComputed(() => params.customization().headerLinks);
        }
    },
    template: `
        <div class="aldi-header">
            <div class="aldi-header-main">
                <div class="aldi-logo">
                    <a data-bind="attr: { href: url }"><img data-bind="attr: { src: logo, alt: title }" /></a>
                </div>
                <div class="aldi-title" data-bind="text: title"></div>
            </div>
            <div class="aldi-header-secondary" data-bind="foreach: headerLinks">
                <a data-bind="attr: { href: url, title: label }, text: label"></a>
            </div>
            <div class="aldi-clearfix"></div>
        </div>
    `
};
