import CustomizationColumnModel from './customizationColumnModel';

export default class CustomizationModel {
    constructor(rawConfig) {
        this.url = rawConfig.custom.url;
        this.logo = rawConfig.custom.logo;
        this.title = rawConfig.custom.title;
        this.headerLinks = rawConfig.custom.headerLinks;
        this.copyright = rawConfig.custom.copyright;

        // TODO come back and do something better later
        this.columns = CustomizationModel._prepareColumns(rawConfig.columns);
    }

    static _prepareColumns(cols) {
        return cols
            .sort((a, b) => a.order - b.order)
            .map(c => new CustomizationColumnModel(c));
    }
}
